<script>
// import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import ProductColorList from '@/components/product/ProductColorList.vue';
import ProductAmountEdit from '@/components/product/ProductAmountEdit.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
  components: { ProductColorList, ProductAmountEdit },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    // gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
      // eslint-disable-next-line no-return-assign
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      return axios.get(` ${API_BASE_URL}/api/products/${this.$route.params.id}`)
      // eslint-disable-next-line no-return-assign
        .then((response) => this.productData = response.data)
      // eslint-disable-next-line no-return-assign
        .catch(() => { this.productsLoadingFailed = true; this.$router.replace({ name: 'notFound' }); })
      // eslint-disable-next-line no-return-assign
        .then(() => { this.productLoading = false; this.currentColor = this.productData.colors[0].id; });
    },
  },
  data() {
    return {
      currentColor: '',
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  watch: {
    color(value) {
      this.currentColor = value;
    },
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
